<template>
  <div class="vertical-center">
    <div class="row flex-column-reverse flex-lg-row m-0">
      <div class="col-lg-8 col-12">
        <div class="row justify-content-center">
          <div class="col-11 col-xl-11  border-lg rounded-lg">
          <h1>Infos</h1>
            <div class="row">
              <div class="col-6 col-lg-6">
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text" id="basic-addon1">Année</span>
                  </div>
                  <input type="text" class="form-control" placeholder="2021" aria-label="Année" aria-describedby="basic-addon1"  v-model="etiquette['year']" v-on:input="changeText(etiquette['year'],writeCanvasDict['year'],clearCanvasDict['year'])">
                </div>
              </div>
              <div class="col-6 col-lg-6">
                <div class="input-group mb-3 v">
                  <div class="input-group-prepend">
                    <span class="input-group-text" id="basic-addon1">Ref</span>
                  </div>
                  <input type="text" class="form-control" placeholder="Reference" aria-label="Ref" aria-describedby="basic-addon1"  v-model="etiquette['reference']" v-on:input="changeText(etiquette['reference'],writeCanvasDict['reference'],clearCanvasDict['reference'])">
                </div>
              </div>
            </div>

            <div class="input-group mb-3 mx-auto">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">Nom du produit</span>
              </div>
              <input type="text" class="form-control" placeholder="Nom du produit" aria-label="Product Name" aria-describedby="basic-addon1"  v-model="etiquette['productName']" v-on:input="changeText(etiquette['productName'],writeCanvasDict['productName'],clearCanvasDict['productName'])">
            </div>
            <div class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">Description</span>
              </div>
              <input type="text" class="form-control" placeholder="Description" aria-label="Description" aria-describedby="basic-addon1"  v-model="etiquette['description']" v-on:input="changeText(etiquette['description'],writeCanvasDict['description'],clearCanvasDict['description'])">
            </div>
            <div v-if="etiquette['labelTheme'] == 'old'" class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">Seconde Desc</span>
              </div>
              <input type="text" class="form-control" placeholder="Seconde Desc" aria-label="Seconde Desc" aria-describedby="basic-addon1"  v-model="etiquette['thirdDescription']" v-on:input="changeText(etiquette['thirdDescription'],writeCanvasDict['thirdDescription'],clearCanvasDict['thirdDescription'])">
            </div>
            <div v-if="etiquette['labelTheme'] == 'old'" class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">Troisieme Desc</span>
              </div>
              <input type="text" class="form-control" placeholder="Troisieme Description" aria-label="Troisieme Description" aria-describedby="basic-addon1"  v-model="etiquette['secondDescription']" v-on:input="changeText(etiquette['secondDescription'],writeCanvasDict['secondDescription'],clearCanvasDict['secondDescription'])">
            </div>
            <div class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">Prix</span>
              </div>
              <input type="text" class="form-control" placeholder="Prix" aria-label="Prix" aria-describedby="basic-addon1"  v-model="etiquette['price']" v-on:input="changeText(etiquette['price'],writeCanvasDict['price'],clearCanvasDict['price'])">

              <select class="form-select" aria-label="Select Currency" v-model="etiquette['currency']" v-on:change="changeText(etiquette['currency'],writeCanvasDict['currency'],clearCanvasDict['currency'])">
                <option v-for="currency in availableCurrencies" v-bind:value="currency" v-bind:key="currency">{{currency}}</option>
              </select>
            </div>

            <div class="input-group mb-3 v">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">Taxes</span>
              </div>
              <input type="text" class="form-control" placeholder="TTC" aria-label="TTC" aria-describedby="basic-addon1"  v-model="etiquette['topRightSquare']" v-on:input="changeText(etiquette['topRightSquare'],writeCanvasDict['topRightSquare'],clearCanvasDict['topRightSquare'])">
            </div>
            <div class="input-group mb-3 v">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">Type</span>
              </div>
              <input type="text" class="form-control" placeholder="M²" aria-label="M²" aria-describedby="basic-addon1"  v-model="etiquette['bottomRightSquare']" v-on:input="changeText(etiquette['bottomRightSquare'],writeCanvasDict['bottomRightSquare'],clearCanvasDict['bottomRightSquare'])">
            </div>
            <button type="button" class="btn btn-success col-md-3 col-lg-3 margin-left" @click="nextLabel">Next</button>
            <button v-if="savedLabel.length !=0" type="button" class="btn btn-success col-md-3 col-lg-3 margin-left" @click="saveCanvas">Save</button>

          </div>
        </div>


      </div>

      <div class="col-12 col-lg-4">
        <h1>Mon Etiquette</h1>
        <div class="row justify-content-center">
          <div class="col-11 col-lg-12">
            <div class="border rounded">
              <canvas id="canvas" width="588" height="260"></canvas>

            </div>
            <div class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">Changer le thème</span>
              </div>
              <select class="form-select" aria-label="Changer le thème" v-model="etiquette['labelTheme']" v-on:change="changeCanvas">
                <option v-for="theme in availableThemes" v-bind:value="theme.value" v-bind:key="theme">{{theme.name}}</option>
              </select>
            </div>    
          </div>
        </div>
    
      </div>
    </div>

  </div>
</template>

<script>
import { jsPDF } from "jspdf";

//Tmura ML Font
export default {
  name: 'Etiquette',
  data: function(){
    return {
      etiquette:{
        labelTheme:"new",
        productName:"",
        reference:"",
        description:"",
        thirdDescription:"",
        secondDescription:"",
        price:"",
        currency:"€",
        topRightSquare:"",
        bottomRightSquare:"",
        year:"2021"
      },
      labelCheckbox:{
        labelTheme:true,
        productName:false,
        reference:false,
        description:false,
        thirdDescription:false,
        secondDescription:false,
        price:false,
        currency:true,
        topRightSquare:false,
        bottomRightSquare:false,
        year:true
      },
      availableCurrencies: ["€","$","£"],
      availableThemes:[{name:"Ancienne Etiquette",value:"old"},{name:"Nouvelle Etiquette",value:"new"}],
      pink: "#F0367D",
      fontName:"DinProMedium",
      clearCanvasDict:{
      },
      writeCanvasDict:{
    },
      savedLabel:[]
    }
  },
  mounted (){
    this.drawCanvas(this.etiquette["labelTheme"])
    document.onreadystatechange = () => {
      if (document.readyState == "complete") {

        this.drawAll()
      }
    }
    setTimeout(this.drawAll(),5000)
  },
  methods:{
    changeText(name,fontData,clear){
      this.clearCanvas(clear)
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = fontData[0]
      ctx.font = fontData[3]
      ctx.textAlign = fontData[4]
      ctx.fillText(name, fontData[1],fontData[2]);
    },
    clearCanvas(data){
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = data[0]
      ctx.fillRect(data[1],data[2],data[3],data[4])

    },
    saveCanvas(){

      var canvas = document.getElementById("canvas");

      var dataURL
      //var newTab = window.open('about:blank','image from canvas');
      //newTab.document.write("<img src='" + dataURL + "' alt='from canvas'/>");
      const doc = new jsPDF("p", "px", [793.706,1122.52]);
      var x = 10
      var y = 10

      for(const i in this.savedLabel){
        this.etiquette = this.savedLabel[i]
        //this.etiquette["productName"]= i
        console.log(this.etiquette)
        if(i%16==0 && i!=0){
          console.log("newPage")
          doc.addPage()
          y=-122
          
        }
        if(i%2 ==0){
          if(i!=0){
            y+=132
            x-=297
          }
          
        }
        else{
          x+=297
        }
        this.changeCanvas()
        dataURL = canvas.toDataURL("image/PNG");
        doc.addImage(dataURL,"png", x, y,294, 130,'',"slow");

      }
      //doc.save("a4.pdf");
      //window.open(doc.output('datauristring'));
      doc.save("a4.pdf")
      this.changeCanvas()
    },
    drawAll(){
      for (const data in this.writeCanvasDict){
        this.changeText(this.etiquette[data],this.writeCanvasDict[data],this.clearCanvasDict[data])
      }
    },
    drawCanvas(name){
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      if(name=="old"){
        this.writeCanvasDict = {
          year:["#F0367D",4,24,"20px DinProMedium"],
          productName:["#F0367D",4,60,"bold 30px DinProMedium","left"],
          reference:["#F0367D",582,24,"bold 20px DinProMedium","right"],
          description:["#F0367D",4,100,"30px DinProMedium","left"],
          thirdDescription:["#F0367D",4,130,"18px DinProLight","left"],
          secondDescription:["#F0367D",4,170,"18px DinProLight","left"],
          currency:["white",266,222,"30px DinProMedium","left"],
          price:["white",252,224,"bold 40px DinProMedium","right"],
          topRightSquare:["white",316,202," 24px DinProMedium","left"],
          bottomRightSquare:["white",316,236," 24px DinProMedium","left"]
        }
        this.clearCanvasDict = {
          year:["white",0,0,100,36],
          productName:["white",0,30,588,40],
          reference:["white",100,0,588,36],
          description:["white",0,70,588,40],
          thirdDescription:["white",0,110,588,30],
          secondDescription:["white",0,146,588,30],
          currency:["#F0367D",260,180,30,60],
          price:["#F0367D",0,180,260,60],
          topRightSquare:["#F0367D",304,178,290,28],
          bottomRightSquare:["#F0367D",304,214,290,28]
        }
        canvas.width=588
        canvas.height=260
        ctx.fillStyle = "white";
        ctx.fillRect(0,0, canvas.width,canvas.height)
        ctx.fillStyle = this.pink
        ctx.fillRect(0,176,296,68)
        ctx.fillRect(300,176,288,32)
        ctx.fillRect(300,212,288,32)
      }
      else if(name=="new"){
        this.writeCanvasDict = {
          year:["white",4,24,"20px MyriadPro-Regular","left"],
          reference:["white",582,24,"bold 16px MyriadPro-Regular","right"],
          description:["white",294,28,"24px MyriadPro-Regular","center"],
          productName:["white",4,62,"bold 30px MyriadPro-Regular","left"],
          currency:["#112655",400,226,"bold 80px MyriadPro-Regular","left"],
          price:["#112655",386,226,"bold 80px MyriadPro-Regular","right"],
          topRightSquare:["#112655",464,184,"bold 20px MyriadPro-Regular","left"],
          bottomRightSquare:["#112655",464,228,"bold 20px MyriadPro-Regular","left"],
        }
        //#112655
        this.clearCanvasDict = {
          year:["#112655",0,0,116,36],
          reference:["#112655",470,0,120,36],
          description:["#112655",120,0,348,36],
          productName:["#112655",0,36,canvas.width,40],
          currency:["white",390,160,60,80],
          price:["white",20,160,372,80],
          topRightSquare:["white",450,166,118,26],
          bottomRightSquare:["white",450,210,118,26],
        }
        canvas.width=588
        canvas.height=260
        ctx.fillStyle = "#112655";
        ctx.fillRect(0,0, canvas.width,canvas.height)
        ctx.fillStyle = "white";
        ctx.fillRect(20,160, canvas.width-40,80)
      }
    },
    changeCanvas(){
      this.drawCanvas(this.etiquette["labelTheme"])
      this.drawAll()
    },
    nextLabel(){
      this.savedLabel.push(JSON.parse(JSON.stringify(this.etiquette)))
      this.cleanLabel()
      this.changeCanvas()
    },
    cleanLabel(){
      for(var i in this.labelCheckbox){
        if(this.labelCheckbox[i]== false){
          this.etiquette[i]=""
        }
      }
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@font-face {
  font-family: "DinProMedium";
  src: local("DinProMedium"),
   url(../fonts/DINPro-Medium.ttf) format("truetype");
}
@font-face {
  font-family: "DinProLight";
  src: local("DinProLight"),
   url(../fonts/DINPro-Light.otf) format("truetype");
}
@font-face {
  font-family: "MyriadPro-Bold";
  src: local("MyriadPro-Bold"),
   url(../fonts/MyriadPro-Bold.otf) format("truetype");
}
@font-face {
  font-family: "MyriadPro-Light";
  src: local("MyriadPro-Light"),
   url(../fonts/MyriadPro-Light.otf) format("truetype");
}
@font-face {
  font-family: "MyriadPro-Regular";
  src: local("MyriadPro-Regular"),
   url(../fonts/MyriadPro-Regular.otf) format("truetype");
}

#canvas{
  width: 100%
}
  
.margin-left {
  margin-left: 5px;
  vertical-align: baseline;
}
.margin-left-form{
  margin-left: 1rem;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

</style>
